@import '../core/variables';
@keyframes bounce {
    70% {
      transform: translateY(0%);
    }
    80% {
      transform: translateY(-15%);
    }
    90% {
      transform: translateY(0%);
    }
    95% {
      transform: translateY(-7%);
    }
    97% {
      transform: translateY(0%);
    }
    99% {
      transform: translateY(-3%);
    }
    100% {
      transform: translateY(0);
    }
  }
  .main {
   
    @media all and (max-width: 768px) {
      background-size: 390px;
    }
    background-image: url("../../images/superbc.png");
    background-size: 650px;
    background-origin: content-box;
    background-repeat: no-repeat;
    background-position: 50%;
    background-position-y: bottom;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* gap: 60px; */
    width: 100%;
    // height: 90vh;
    background-color: $newBlue;
    &__h1 {
      display: none;
    }
    &__img {
      background-image: url("../../images/woman_crop.png");
      background-position: center;
      object-fit: cover;
      background-size: 300px;
      height: 250px;
      width: 280px;
  
      margin: 30px 35px 35px 35px;
      @media all and (min-width: 768px) {
        /*  width: 419px;
        height: 105px; */
        /* margin-top: 50px; */
        margin: 0px 0px 15px 0px;
        position: relative;
        //top: 50px;
      }
    }
    &__title {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      &--create {
        color: $yellow;
        font-family: $landingTitle;
        font-size: 19.2px;
        font-weight: bold;
        @media all and (min-width: 768px) {
          font-size: 24px;
        }
      }
      &--text {
        color: whitesmoke;
        font-family: $mainFont;
        font-size: 13px;
        text-align: center;
        white-space: pre-line;
        margin: 0px 27px 0px 27px;
        @media all and (min-width: 768px) {
          font-size: 16px;
          width: 331px;
        }
      }
    }
    &__options {
      display: flex;
      border: solid rgba(whitesmoke, $alpha: 0.3) 1.6px;
      border-radius: 4px;
      padding: 15px 20px 17.8px 31.5px;
      margin: 17.8px 26px 60px 26px;
      list-style-type: none;
      @media all and (min-width: 768px) {
        width: 325px;
        height: 86px;
        justify-content: space-around;
        margin: 17.8px 26px 18px 26px;
      }
      @media all and (min-width: 1200px) {
        margin: 17.8px 26px 38px 26px;
      }
      &--list {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        text-transform: capitalize;
        color: whitesmoke;
        font-family: $mainFont;
        font-size: 12.8px;
        list-style-type: none;
        gap: 30px;
        margin-top: 15px;
        margin-bottom: 10px;
        @media all and (min-width: 768px) {
          gap: 51px;
          font-size: 16px;
        }
      }
    }
  
    .cop {
      display: flex;
      flex-direction: column;
    }
  
    .fa-icon {
      color: whitesmoke;
      font-size: 22.4px;
      @media all and (min-width: 768px) {
        font-size: 28px;
      }
    }
    &__start {
      animation: bounce 2s ease infinite;
      display: flex;
      cursor: pointer;
      margin: 38px 101px 30px 90px;
      border-radius: 21px;
      text-transform: uppercase;
      background-color: $newBlue;
      color: white;
      border: 1px solid whitesmoke;
  
      @media all and (min-width: 768px) {
        margin: 213px 0 30px 0;
      }
      &--create {
        text-decoration: none;
        color: white;
        font-weight: bold;
        font-size: 16px;
        font-family: $mainFont;
        padding: 10px 20px;
      }
    }
  }
  
  .bg {
    @media all and (max-width: 768px) {
      position: relative;
      transform: translateY(-20px);
      width: 85%;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 600px;
  
    padding: 15px;
    border-radius: 10px;
  }